export default {
	data() {
		return {
			actId: null,
			peopleList: [],
			isShowNoData: false
		}
	},
	onLoad(options) {
		this.actId = options.actId
		this.$http.get({
			url: '/v1/rest/home/activitiesEnrolled',
			data: {
				activitiesId: this.actId
			}
		}).then(resp => {
			// console.log(resp)
			if(resp.status == 200) {
				this.peopleList = resp.data
				if(this.peopleList.length == 0) {
					this.isShowNoData = true
				}else{
					this.isShowNoData = false
				}
			}
		})
	}
}
